nav * {
  margin: 0;
  padding: 0 2px;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

nav {
  display: flex;
  width: 100%;
  background: #0a5071;
  position: relative;
  justify-content: space-evenly;
  text-align: center;
  padding: 15px 30px;
}

nav .icon {
  font-size: 35px;
  font-weight: 800;
  color: #fff;
  cursor: pointer;
}

nav div.icon b {
  color: red;
}

nav ul {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: auto 0;
}

nav ul li {
  margin: 0 2px;
  justify-content: flex-end;
}

nav ul li a {
  color: #fff;
  font-size: 20px;
  text-decoration: none;
  text-transform: capitalize;
  letter-spacing: 1px;
  padding: 5px 10px;
}

nav ul li:hover Link {
  background: #fff;
  color: #0a5071;
}

nav .seach-box {
  display: flex;
  margin: auto 0;
  height: 35px;
  line-height: 35px;
}

nav .search-box input {
  border: none;
  outline: none;
  background: #fff;
  height: 100%;
  padding: 0 10px;
  font-size: 20px;
  width: 350px;
}

nav .seach-box span {
  color: #0a5071;
  font-size: 20px;
  background: #fff;
  height: 100%;
  padding: 0 5px;
  position: relative;
  cursor: pointer;
  z-index: 1;
}

nav .search-icon {
  font-size: 20px;
  padding: 0 4px;
}

nav .search-box span:hover {
  color: #fff;
}

nav .seach-box span::after {
  height: 100%;
  width: 0%;
  content: '';
  background: #ff003c;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  transition: 0.3s;
}

nav .search-box span:hover::after {
  width: 100%;
}

nav .search-box span:hover::after {
  width: 100%;
}

nav .bar {
  position: relative;
  margin: auto;
  display: none;
}

nav .bar span {
  position: absolute;
  color: #fff;
  font-size: 35px;
}

input[type="checkbox"] {
  display: none;
}

@media  screen and (max-width: 1250px) {
  nav {
    display: block;
    padding: 0;
  }

  nav .icon {
    display: inline-block;
    padding: 15px 30px;
  }

  nav .seach-box{
    width: 100%;
    display: inline-flex;
    justify-content: center;
    margin-bottom: 25px;
  }

  nav .search-box input {
    width: 90%;
  }

  nav ul {
    display: flex;
    flex-direction: column;
    background: #fff;
    height: 0;
    visibility: hidden;
    transition: 0.3s;
  }

  nav ul li {
    text-align: center;
    transition: 0.3s 0.1s all;
    opacity: 0;
  }
  
  nav ul li a {
    color: #000;
    font-size: 28px;
    padding: 25px;
    display: block;
  }

  nav ul li:nth-child(1) {
    transform: translateX(-150px);
  }
  nav ul li:nth-child(2) {
    transform: translateX(-200px);
  }
  nav ul li:nth-child(3) {
    transform: translateX(-250px);
  }
  nav ul li:nth-child(4) {
    transform: translateX(-300px);
  }
  nav ul li:nth-child(5) {
    transform: translateX(-350px);
  }
  nav ul li:nth-child(6) {
    transform: translateX(-400px);
  }
  nav ul li:nth-child(7) {
    transform: translateX(-400px);
  }

  nav .bar {
    display: block;
    position: absolute;
    top: 20px;
    right: 80px;
    cursor: pointer;
  }

  nav .bar #times {
    display: none;
  }

  #check:checked ~ nav .bar #times {
    display: block;
  }

  #check:checked ~ nav .bar #bars {
    display: none;
  }

  #check:checked ~ nav ul {
    visibility: visible;
    height: 465px;
  }

  #check:checked ~ nav ul li:nth-child(1),
  #check:checked ~ nav ul li:nth-child(2),
  #check:checked ~ nav ul li:nth-child(3),
  #check:checked ~ nav ul li:nth-child(4),
  #check:checked ~ nav ul li:nth-child(5),
  #check:checked ~ nav ul li:nth-child(6),
  #check:checked ~ nav ul li:nth-child(7) {
    transform: translateX(0);
    opacity: 1;
  }
  
}