.modal {
    position: fixed;
    top: 30px;
    left: 0;
    z-index: 1050;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;
}

.modal-backdrop.show {
    opacity: .5;
}

.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1040;
    width: 100vw;
    height: 100vh;
    background-color: #000;
}

.fade {
    transition: opacity 15s linear;
}

.modal-dialog {
    max-width: 500px;
    position: relative;
    width: auto;
    margin: 15rem auto;
    pointer-events: none;
}

.modal-dialog-centered {
    min-height: calc(100% - 1rem);
}

.modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: .3rem;
    outline: 0;
    top: -70px;
}

.modal-body {
    position: relative;
    flex: 1 1 auto;
    padding: 1rem 2rem;
}

.form-row {
    padding: 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: 15px;
    margin-right: 15px;
}

.col {
    flex-grow: 1;
    max-width: 90%;
}

.form-row > .col {
    padding-right: 5px;
    padding-left: 5px;
    margin-right: 10px;
    margin-top: 25px;
}