.actions-menu {
    padding: 45px 150px;
}

.actions-menu h1 {
    margin-bottom: 20px;
}

.actions-menu-content {
    display: flex;
    justify-content:baseline;
}

.btn {
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;

}

.actions-menu .btn-primary {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
    height: 45px;
}

.actions-menu-content.search-field {
    display: block;
    justify-content: space-around;
}

.actions-menu input{
    display: inline-block;
    justify-content: space-evenly;
    font-weight: 400;
    color: gray;
    border: 1px solid gainsboro;
    border-radius: .25rem;
    padding: 2px 1px 2px;
    width: 10rem;
    height: 2.5rem;
    margin-left: 30px;
}

span.search-icon {
    font-size: 25px;
    padding: 0 4px;
    cursor: pointer;
    margin: auto;
    margin-left: 0.5rem;
    height: fit-content;
}

input::placeholder {
    font-size: medium;
    justify-content:center;
}
